* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.App {
  height: 100vh;
  overflow: hidden;
}

h1 {
  text-align: center;
  padding: 1rem;
  background: rgba(0, 0, 0, 0.8);
  margin: 0;
  position: absolute;
  width: 100%;
  z-index: 10;
} 